.softwareContainer{
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap;
}

.softwareElement{
	background:rgba(0,0,0,.05);
	border:.25px solid rgba(0,0,0,.5);
	height:100px;
	width:100px;
	display: flex;
	justify-content: center;
}
.softwareTitleTop{
	font-family: sans-serif;
	font-size: 20px;
	letter-spacing: 2px;
	color:rgba(0,0,0,.5);
	padding-bottom: 10px;
}