.skillsContainer{
	border-top: .5px solid rgba(0,0,0,.3);
	display:grid;
	grid-template-columns: 1fr 8fr;
}

.skillsTitle{
	display: flex;
	color:rgba(0,0,0,.5);
	justify-content: center;
	align-items: center;
	background: rgba(0,0,0,.1);
}

.skillsList{
	border-left: .5px solid rgba(0,0,0,.3);
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap;
	padding:10px;
	width:88%;
}

.skillsList div div{
	margin:5px;
	padding:5px;
	width:220px;
	border-radius: 2px;
	border:.5px solid rgba(0,0,0,.2);
}

.skillsbutton{
	border:.5px solid rgba(0,0,0,.5);
}

.skillsbutton:hover{
	background:rgba(0,0,0,.05);
}

.electroSkillsList{
	display:flex;
	flex-wrap: wrap;
	justify-content: space-around;
	animation: fadeIn 1s linear;
}

.electricalElement{
	height:160px;
	width:160px;
	border:.5px solid rgba(0,0,0,.3);
	margin:5px;
	display:flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
}

.closeBtn{
	background:rgba(255,255,255,1);
	border:1px solid rgba(0,0,0,.3);
	padding:5px;
}

.digital{
	background-image: url(digital.jpg);
	background-position: center;
	background-size: cover;
}


.power{
	background-image: url(power.jpg);
	background-position: center;
	background-size: cover;	
}
.comms{
	background-image: url(comms.jpg);
	background-position: center;
	background-size: cover;	
}
.robot{
	background-image: url(robotics.jpg);
	background-position: center;
	background-size: cover;	
}

.micro{
	background-image: url(micro.jpg);
	background-position: center;
	background-size: cover;	
}
.instrumentation{
	background-image: url(instrumentation.jpg);
	background-position: center;
	background-size: cover;	
}

.electricalText{
	background: rgba(0,0,0,.5);
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	transform: skewY(-45deg);
	height:80px;
	width:500px;
}

.digitalTextInner{
	transform: skewY(45deg) rotate(-45deg);
	color:rgba(255,255,255,.8);
	font-size: 20px;
	font-weight: 700;
	font-family: sans-serif;
}

.softwareImage img{
	height:40px;
	width:auto;
}

.sdSkillsList{
	display:flex;
	justify-content: space-around;
	flex-wrap: wrap;
	animation: fadeIn 1s linear;
}
.softwareElement{
	margin:5px;
	border:.5px solid rgba(0,0,0,.4);
	height:100px;
	width:100px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.QualitySystemsContainer{
	display: flex;
	justify-content: space-around;
	width:100%;
	flex-wrap: wrap;
}

.qualityElement{
	height:150px;
	width:150px;
	background: white;
	border:.5px solid rgba(0,0,0,.2);
	margin:5px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

a{
	text-decoration: none;
}

.detailsBtn{
	background:rgba(0,0,0,.3);
	margin-top:25px;
	padding:5px;
	border-radius: 3px;
	color:white;
}

.sixSigma{
	background:#f1c40f;
	color:white;

}

.wm{
	background:#9b59b6;
	color:white;
}

.doe{
	background:#3498db;
	color:white;
}

.as{
	background:#e74c3c;
	color:white;
}

.automationTitle{
	background:rgba(0,0,0,.4);
	margin:5px;
	padding:5px;
}

.listElement{
	background: rgba(0,0,0,.1);
	margin:5px;
	padding:5px;
}

.autoSoftwares{
	display:flex;
	justify-content: space-around;
}

.automationContainer{
	width:100%;
}

.mlSkillsList{
	animation: fadeIn 1s linear;
}

.mltitle{
	background:rgba(0,0,0,.4);
	margin:5px;
	padding:5px;
}

.mlelement{
	background:rgba(0,0,0,.1);
	margin:5px;
	padding:5px;
}

.mlSection{
	display: flex;
	justify-content: space-around;
}

.mfgImg img{
	height:200px;
	width:222px;
}

.mfgSkillsList{
	display:flex;
	justify-content: space-around;
	flex-wrap: wrap;
	animation: fadeIn 1s linear;
}

.mfgElement{
	border:.5px solid rgba(0,0,0,.2);
	margin:10px;
	padding:5px;
	border-radius: 5px;
	color:rgba(0,0,0,.7);
}