.experienceTitle{
	color:rgba(0,0,0,.4);
	padding-top:30px;
	padding-bottom:30px;
	font-family: sans-serif;
	font-size: 18px;
	letter-spacing: 3px;
}

.workExperienceNav{
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

.experienceBtn{
	color:rgba(0,0,0,.6);
	background:rgba(0,0,0,.04);
	border:.5px solid rgba(0,0,0,.2);
	height:120px;
	width:120px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border-radius: 100%;
}

.experienceBtn:hover{
	background: rgba(0,0,0,.08);
}

.closeBtnExp{
	background:rgba(0,0,0,.1);
	border:.5px solid rgba(0,0,0,.3);
	border-radius: 100%;
	width:60px;
	height:60px;
	padding:10px;
	margin:10px;
	margin-left:auto;
	margin-right:auto;
	display: flex;
	justify-content: center;
	align-items: center;
	color:rgba(0,0,0,.7);
}

.positionFormat{
	text-align: left;
	line-height: 200%;
	width:90%;
	margin-left:auto;
	margin-right:auto;
	padding:10px;
	font-family: sans-serif;
	animation: 1s fadeIn linear;
}

.positionFormatElement{
	color:rgba(0,0,0,.7);
}

@keyframes fadeIn{
	from{
		opacity:0;
	}
	to{
		opacity:1;
	}
}

.responsibility{
	width:90%;
	margin-left:auto;
	margin-right:auto;
	margin-top:10px;
}