.educationContainer{
	border-top:1px solid rgba(0,0,0,.2);
	margin-top:40px;
	color:rgba(0,0,0,.6);
	font-family: sans-serif;
	font-size: 19px;
	letter-spacing: 2px;
	display:grid;
	grid-template-columns: 1fr 8fr;
	margin-bottom: 40px;
	border-bottom:1px solid rgba(0,0,0,.2);
}



.educationContainerInner{
	background: white;
	display: flex;
	justify-content: space-around;
	align-items: center;
	width:88%;
	margin-right: auto;
}

.educationTitle{
	border-right: 1px solid rgba(0,0,0,.2);
	font-size: 15px;
	letter-spacing: 0px;
	display: flex;
	justify-content: center;
	align-items: center;
	background:rgba(0,0,0,.08);
	color:rgba(0,0,0,.5);
}

.educationBtn{
	font-size: 14px;
	width:100px;
	background: rgba(0,0,0,0.06);
	border:.5px solid rgba(0,0,0,.2);
	height:60px;
	margin:5px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
}

.educationDisplay{
	text-align: left;
	width:100%;
	font-size: 15px;
	font-family: sans-serif;
	animation: fadeIn 1s linear;
}

.educationElement{
	margin:5px;
	padding:5px;
}

.educationClose{
	background:rgba(0,0,0,.05);
	border:.5px solid rgba(0,0,0,.3);
	width:60px;
	border-radius: 5px;
	margin-left:8px;
}