.introMain{
	display:grid;
	grid-template-columns: 1fr 1fr;
	font-size:18px;
	font-family: sans-serif;
	border-bottom: .5px solid rgba(0,0,0,.3);
}

.name{
	display: flex;
	justify-content: center;
	align-items: center;
	border-right:.5px solid rgba(0,0,0,.3);
	color:rgba(0,0,0,.5);
}

.contactBox{
	display: flex;
	justify-content: center;
	font-size: 90%;
	color:rgba(0,0,0,.5);
	padding:7px;
}

.contactBox img{
	height:40px;
	width:auto;
	background: transparent;
	position: relative;
	z-index: -1;
}


.contactBox:hover{
	background:rgba(0,0,0,.1);
	color:black;
	transition:all .2s;
}

.contactForm{
	border-bottom: 1px solid rgba(0,0,0,.2);
}